import { withStyles } from '@material-ui/core/styles'
import Avatar from '@material-ui/core/Avatar'
import Divider from '@material-ui/core/Divider'
import Gravatar from 'react-gravatar'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Paper from '@material-ui/core/Paper'
import React, { Component } from 'react'
import Typography from '@material-ui/core/Typography'

import FullScreenLoader from '../../Components/FullScreenLoader'
import ItemsContainer from '../../Containers/ItemsContainer'
import ItemsGrid from '../../Components/ItemsGrid'
import styles from './styles'

class ProfilePage extends Component {
  render () {
    const { classes } = this.props
    return (
      <div className={classes.root}>
        <ItemsContainer userId={this.props.match.params.userId}>
          {({ userItemsData: { loading, user } }) => {
            if (loading) return <FullScreenLoader inverted />
            const { borrowed, items } = user
            return (
              <React.Fragment>
                <Paper className={classes.profileHeader}>
                  <header>
                    <div>
                      <Typography
                        variant='display2'
                        gutterBottom
                        style={{ display: 'flex' }}
                      >
                        <Gravatar
                          email={user.email}
                          size={50}
                          className={classes.gravatarImage}
                          style={{ marginRight: '1rem' }}
                        />
                        {' '}
                        {user.fullname}
                      </Typography>
                      <Typography gutterBottom variant='title'>
                        <span>{items.length}</span>
                        {' '}
                        {' '}
                        {items.length !== 1 ? 'Items' : 'Item'}
                        &nbsp;shared
                        {' '}
                        <span>{borrowed.length}</span>
                        {' '}
                        {' '}
                        {borrowed.length !== 1 ? 'Items' : 'Item'}
                        &nbsp;borrowed
                      </Typography>
                      <Typography
                        variant='subheading'
                        style={{ marginBottom: '2rem' }}
                      >
                        {user.bio}
                      </Typography>
                      {borrowed.length > 0
                        ? <React.Fragment>
                          <Typography gutterBottom variant='button'>
                              Currently borrowing:
                            </Typography>
                          <List className={classes.infoList}>
                            {borrowed.map(item => (
                              <React.Fragment key={item.id}>
                                <ListItem>
                                  <Avatar>
                                    <img
                                      src={item.imageurl}
                                      style={{ maxWidth: '100%' }}
                                      />
                                  </Avatar>
                                  <ListItemText
                                    primary={item.title}
                                    secondary={item.itemowner.fullname}
                                    />
                                </ListItem>
                                <li>
                                  <Divider inset />
                                </li>
                              </React.Fragment>
                              ))}
                          </List>
                        </React.Fragment>
                        : ''}
                    </div>
                    <div className={classes.profileMeta}>
                      <div className={classes.shareStats}>
                        <Typography variant='display2'>
                          {items.length}{' '}
                        </Typography>
                        <Typography variant='subheading'>
                          {items.length !== 1 ? 'Items' : 'Item'}
                          &nbsp;shared
                        </Typography>
                        <Typography variant='display2'>
                          {borrowed.length}{' '}
                        </Typography>
                        <Typography variant='subheading'>
                          {borrowed.length !== 1 ? 'Items' : 'Item'}
                          &nbsp;borrowed
                        </Typography>
                      </div>
                      <Gravatar
                        email={user.email}
                        size={200}
                        className={classes.gravatarImage}
                      />
                    </div>
                  </header>
                </Paper>
                {items.length &&
                  <React.Fragment>
                    <Typography color='primary' variant='display1' gutterBottom>
                      Shared Items
                    </Typography>
                    <ItemsGrid items={items} />
                  </React.Fragment>}
              </React.Fragment>
            )
          }}
        </ItemsContainer>
      </div>
    )
  }
}

export default withStyles(styles)(ProfilePage)



